import "./styles.css";
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";
import { useState } from "react";

export default function App() {
  const [tasks, setTask] = useState([
    { id: 1, text: "abs", day: "sixt July 2020", reminder: true },
    { id: 2, text: "cde", day: "sixt July 2021", reminder: true },
    { id: 3, text: "fgh", day: "sixt July 2022", reminder: false }
  ]);

  //Add Task
  const addTask = (task) => {
    const id = Math.floor(Math.random() * 1000) + 1;
    const newTask = { id, ...task };
    setTask([...tasks, newTask]);
  };

  const deleteTask = (id) => {
    setTask(tasks.filter((ele) => ele.id !== id));
  };

  const toggleReminder = (id) => {
    setTask(
      tasks.map((ele) =>
        ele.id === id ? { ...ele, reminder: !ele.reminder } : ele
      )
    );
  };

  return (
    <div className="App">
      <Header />
      <AddTask onAdd={addTask} />
      {tasks.length > 0 ? (
        <Tasks task={tasks} onDelete={deleteTask} onToggle={toggleReminder} />
      ) : (
        "No task to show"
      )}
    </div>
  );
}
